.icon {
  position: relative;

  display: block;
  width: 100%;
  padding-bottom: 100%;

  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  text-decoration: none;

  &__tooltip {
    display: none;
  }

  @include onTablet {
    &__tooltip {
      position: absolute;
      right: 100%;
      top: 50%;
      transform: translateY(-50%);
      display: block;
      width: max-content;
      padding-right: 20px;
      font-weight: 12px;
      line-height: 16px;
      color: #fff;
      letter-spacing: 2px;
      text-transform: uppercase;
      visibility: visible;
      
      opacity: 0;
      pointer-events: none;
    }
  
    &:hover &__tooltip {
      opacity: 1;
      pointer-events: all;
    }
  }
  

