@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onTablet {
  @media (min-width: 744px) {
    @content;
  }
}

@mixin onDekstop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin top-actions {
  --button-width: 100px;
  display: grid;
  grid-template-columns: 1fr var(--button-width);
  justify-items: space-around;
  align-items: center;
  gap: 40px;

  @include onTablet {
    --icon-width: 24px;
  }
}

@mixin container {
  margin: 0 auto;
  max-width: 1020px;
  padding: 0 20px;

  @include onDekstop {
    padding: 0 40px;
  }
}

@mixin square($size) {
  width: $size;
  height: $size;
}

@mixin rectangle($width, $height) {
  width: $width;
  height: $height;
}

